import { NextResponse } from "next/server";
import Course from "../../../../models/Course";
// import Course from "../../../../models/Course";
// import { connectDB } from "../../../../lib/db";


// UPDATE course
export async function PUT(req: Request, context: { params: { id: string } }) {
  try {
    const { id } = context.params;
    const body = await req.json();
    const updatedCourse = await Course.findByIdAndUpdate(id, body, { new: true });
    if (!updatedCourse) return NextResponse.json({ success: false, message: "Course not found" }, { status: 404 });
    return NextResponse.json({ success: true, course: updatedCourse });
  } catch (err) {
    return NextResponse.json({ success: false, message: err }, { status: 500 });
  }
}

// DELETE course
export async function DELETE(_: Request, context: { params: { id: string } }) {
  try {
    const { id } = context.params;
    const deleted = await Course.findByIdAndDelete(id);
    if (!deleted) return NextResponse.json({ success: false, message: "Course not found" }, { status: 404 });
    return NextResponse.json({ success: true });
  } catch (err) {
    return NextResponse.json({ success: false, message: err }, { status: 500 });
  }
}
